.TodoList {
  display: flex;
  flex-direction: column;
}
input,
button {
  font-size: inherit;
  padding: 10px;
}
h4 {
  margin-top: 0;
}
.items {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.placeholder {
  opacity: 0.6;
  font-size: 0.8em;
}
.loader {
  display: flex;
  justify-content: center;
}
.filter {
  display: flex;
  justify-content: left;
  text-align: left;
}
.username {
  font-size: 0.8em;
}
.greeting {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 0.66rem;
}
